import React from "react";
import OutsideClickHandler from "react-outside-click-handler";
import { EntityInstance, ContentBlock } from "draft-js";
import { TextContext } from "../../../../duck/context";
import { ImageMenu } from "./components";
import styles from "./Image.module.css";

interface ImageType {
  entity: EntityInstance;
  block: ContentBlock;
}

const Image: React.FC<ImageType> = ({ entity, block }) => {
  const { editMode } = React.useContext(TextContext);
  const [opened, setOpened] = React.useState(false);
  const { src } = entity.getData();
  const key = block.getKey();

  const clickHandler = () => {
    if (editMode) {
      setOpened((prevOpened) => !prevOpened);
    }
  };

  const outsideClickHandler = () => {
    setOpened(false);
  };

  return (
    <OutsideClickHandler onOutsideClick={outsideClickHandler}>
      <div className={styles.wrapper}>
        <img
          src={src}
          alt=""
          onClick={clickHandler}
          className={opened ? styles.opened : ""}
        />
        {opened && <ImageMenu blockKey={key} />}
      </div>
    </OutsideClickHandler>
  );
};

export default Image;
