import React from "react";
import Button from "../../../../../../../../../button";
import { TextContext } from "../../../../../../duck/context";
import icon from "./assets/delete.svg";
import { removeImage } from "./duck/operations";
import styles from "./ImageMenu.module.css";

interface ImageMenuType {
  blockKey: string;
}

const ImageMenu: React.FC<ImageMenuType> = ({ blockKey }) => {
  const { editorState, setEditorState } = React.useContext(TextContext);

  const removeHandler = (): void => {
    setEditorState(removeImage(editorState, blockKey));
  };

  return (
    <div className={styles.wrapper}>
      <Button small onClick={removeHandler}>
        <img src={icon} alt="" />
      </Button>
    </div>
  );
};

export default ImageMenu;
